// colors
[data-dark="false"] {
  --primary: #003366;      /* 深蓝作为强调色（按钮、链接、标题） */
  --secondary: #4a6fa5;    /* 次要蓝灰 */
  --text: #222222;         /* 深灰文字，比纯黑更柔和 */
  --background: #ffffff;   /* 主背景白色 */
  --background-alt: #f8f9fb; /* 卡片/分区浅灰，层次感 */
  --light-gray: #e5e5e5;
  --gray: #7a7a7a;
  --dark-gray: #3a3a3a;
  --overlay: #00000010;
}

[data-dark="true"] {
  --primary: #4a90e2;      /* 夜间蓝色点缀 */
  --secondary: #2c3e50;    /* 深灰蓝 */
  --text: #e0e0e0;         /* 柔和白字 */
  --background: #181a1b;   /* 深灰背景（不是纯黑，避免压抑） */
  --background-alt: #222426; 
  --light-gray: #444444;
  --gray: #8c8c8c;
  --dark-gray: #c0c0c0;
  --overlay: #ffffff10;
}

:root {
  // font families
  --title: "Merriweather", serif;  /* 标题：学术衬线字体 */
  --heading: "Lora", serif;
  --body: "Inter", sans-serif;     /* 正文：清晰易读 */
  --code: "Roboto Mono", monospace;

  // font sizes
  --large: 1.1rem;
  --xl: 1.3rem;
  --xxl: 1.5rem;

  // font weights
  --thin: 300;
  --regular: 400;
  --semi-bold: 600;
  --bold: 700;

  // text line spacing
  --spacing: 1.8;   
  --compact: 1.5;

  // effects
  --rounded: 2px;   
  --shadow: 0 2px 6px 0 var(--overlay);
  --transition: 0.2s ease;
}
